<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者：{{blog.author}}</p>
    <p>分类:</p>
    <ul>
      <li v-for="category in blog.categories">
        {{category}}
      </li>
      <!-- 这就是对应所有展示内容 -->
    </ul>
    <button @click="deleteSingleBlog()">删除</button>
    <router-link :to="'/edit/'+id"><button>编辑</button></router-link>
    </article>
  </div>
</template>

<script>
  export default{
    name:"single-blog",
    data(){
      return{
        id:this.$route.params.id,
        // 拿到路由后的id参数
        blog:{}
        //拿到单个blog数据是对象
      }
    },
    created(){
      this.$http.get('https://vue-demo1-185af.firebaseio.com/posts/'+this.id+'.json')
      .then(function(data){
        console.log(data);
        // return data.json();
        this.blog=data.body;
      })
      // .then(function(data){
      //   this.blog=data;
      // })
    },
    methods:{
      deleteSingleBlog(){
        this.$http.delete('https://vue-demo1-185af.firebaseio.com/posts/'+this.id+'.json')
        .then(response=>{
          this.$router.push({path:'/'})
        })
      }
    }
  }
</script>

<style>
  #single-blog{
    max-width:960px;
    margin:0px auto;
    padding:20px;
    background: #eee;
    border:1px solid #aaa;
  }
</style>



