<template>
  <div id="edit-blog">
    <h2>编辑博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required/>
      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxs">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label>Angular4</label>
        <input type="checkbox" value="Angular4" v-model="blog.categories">
      </div>
      <label>作者:</label>
      <select v-model="blog.author">
        <option v-for="author in authors">
          {{author}}
        </option>
      </select>
      <button v-on:click.prevent="post">编辑博客</button>
      <!-- 这里prevent阻止页面刷新 -->
    </form>

    <div v-if="submmited">
      <h3>您的博客编辑成功！</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容</p>
      <p>{{blog.content}}</p>
      <p>博客分类</p>
      <ul>
        <li v-for="category in blog.categories">
          {{category}}
        </li>
      </ul>
      <p>作者:{{blog.author}}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: 'edit-blog',
  data () {
    return {
      id:this.$route.params.id,
      blog:{},
      authors:["Hemiah","Henry","Bucky"],
      submmited:false
    }
  },
  methods:{
    fetchData(){
      console.log(this.id);
      this.$http.get('https://vue-demo1-185af.firebaseio.com/posts/'+this.id+'.json')
      .then(response=>{
        // console.log(response.body);
        this.blog=response.body;
      })
    },
    post:function(){
      // http://jsonplaceholder.typicode.com/
      // http://jsonplaceholder.typicode.com/posts
      this.$http.put('https://vue-demo1-185af.firebaseio.com/posts/'+this.id+'.json',this.blog)
      .then(function(data){
        // 编辑用put 编辑指定的地址用上边的地址
        console.log(data);
        this.submmited=true;
      });
    }
  },
  created(){
    this.fetchData();
  }
}
</script>

<style scoped>
  #edit-blog *{
    box-sizing:border-box;
  }
  #edit-blog{
    margin:20px auto;
    max-width:600px;
    padding:20px;
  }
  label{
    display:block;
    margin:20px 0 10px;
  }
  input[type="text"],textarea,select{
    display:block;
    width:100%;
    padding:8px;
  }
  textarea{
    height:200px;
  }
  #checkboxs label{
    display:inline-block;
    margin-top:0;
  }
  #checkboxes input{
    display: inline-block;
    margin-right:10px;
  }
  /* 独占据一行用display:block;
  多个一行用  display:inline-block; */
  button{
    display:block;
    margin: 20px 0;
    background:crimson;
    color:#fff;
    border:0px;
    padding:14px;
    border-radius:4px;
    font-size: 18px;
    cursor:pointer;
  }
  #preview{
    padding:10px 20px;
    border:1px dotted #ccc;
    margin:30px 0;
  }
  h3{
    margin-top: 10px;
  }
</style>



