<template>
  <div v-theme:column="'narrow'" id="show-blogs">
    <!-- v-theme给值要看是对象还是字符串或者是数组
     字符串需要写成上边的格式.和一般的指令一样，:后边
     也可传参后边用binding.arg-->
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索">
    <!-- <div v-for="blog in blogs" class="single-blog">
      想要实现下边的搜索功能不能便利blogs了因为这样filteredBlogs不能触发 -->
      <div v-for="blog in filteredBlogs" class="single-blog">
        <!-- 需要遍历filteredBlogs -->
      <router-link v-bind:to="'/blog/'+blog.id"><h2 v-rainbow>{{blog.title | to-uppercase}}</h2></router-link>
      <!-- to在跳转路由时需要绑定成属性，用字符串拼接 配合singleblog拿到id可以显示-->
      <!-- 用后边的过滤器必须在后边加"|"这个管道符号，后边是管道名称 -->
      <!-- v-rainbow和上边的v-theme都是自定义指令
      可以给值也可以不给值 -->
      <article>
        {{blog.content | snippet}}
        <!-- 这里拿到的属性没有body变成content -->
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: 'show-blogs',
  data () {
    return {
      blogs:[],
      search:""
    }
  },
  created(){
    // this.$http.get("http://jsonplaceholder.typicode.com/posts")
    this.$http.get("https://vue-demo1-185af.firebaseio.com/posts.json")
    .then(function(data){
        // console.log(data);
        // console.log(data.json());
      return data.json()
      // this.blogs=data.body.slice(0,10);
      // console.log(this.blogs);
    }).then(function(data){
      var blogsArray=[];
      for(let key in data){
        // console.log(key);
        // console.log(data[key]);
        data[key].id=key;
        // 这一步是把key值边id值key是数据库生成的标识值
        // data[key]可以看到具体的数据了
        blogsArray.push(data[key]);
      }
      // console.log(blogsArray);
      this.blogs=blogsArray;
       // console.log(this.blogs);
    })
  },
  computed:{
    filteredBlogs:function(){
      return this.blogs.filter((blog)=>{
        return blog.title.match(this.search);
        // blog.title是按照标题匹配还可以按照其他匹配
      })
      // fiter相当于把所有的内容过一遍match相当于找匹配，返回
      // 所要匹配到的这两个都是es6方法
    }
  },
  // filters:{
  //   "to-uppercase":function(value){
  //     return value.toUpperCase();
  //   }
  // }
  filters:{
    toUppercase(value){
      return value.toUpperCase();
    }
  },
  // 这是局部的过滤器，下边是es6写法上边是老方法
  directives:{
    'rainbow':{
      bind(el,binding,vnode){
        el.style.color="#"+Math.random().toString(16).slice(2,8);
      }
    }
  }
  // 这就是局部自定义模板指令
}
</script>

<style scoped>
  #show-blogs{
    max-width:800px;
    margin: 0 auto;
  }
  .single-blog{
    padding: 20px;
    margin:20px 0;
    box-sizing:border-box;
    background: #eee;
    border:1px dotted #aaa;
  }
  #show-blogs a{
    color:#444;
    text-decoration:none;
  }
  input[type="text"]{
    padding:8px;
    width:100%;
    box-sizing:border-box;
  }

</style>



